	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Experimental Failure","date":"25 Jan 2002","slug":"/2002-01-25-experimental-failure/","tags":"Y2002"},"html":"<p>Congratulations to <a href=\"http://jim.roepcke.com/\">Jim and Cheryl Roepke</a> on the <a href=\"http://jim.roepcke.com/2002/01/24\">birth of their son</a> (with picture!). At just over 10 pounds, he's in Christopher's weight class.</p>\n<p>Last night we tried removing the two removable rods from the side of Christopher's crib, since he can crawl over anyway. He found the new exit fascinating and refused to go to sleep, even after his parents wanted to. Finally at 10:30 pm, we put the rods back in. He crawled out one more time, and finally fell asleep. <b>Postscript:</b> As I was writing this, I head a crash and cry from upstairs. Christopher is up and raring to go at 6:30 am! He's sure going to nap well this afternoon. I hope.</p>\n<p>Of course, the <a href=\"http://radio.userland.com/xmlCoffeeMug\">XML Coffee Mug</a> won't work for people running Radio remotely. Then again, people smart enough to run Radio remotely are smart enough to set up a redirect from 127.0.0.1:5335 to whereever their Radio is running.</p>"},"next":{"fields":{"title":"We Want The Airwaves For SMS","date":"24 Jan 2002","slug":"/2002-01-24-we-want-the-airwaves-for-sms/"}},"previous":{"fields":{"title":"Immigrants? I See No Immigrants","date":"25 Jan 2002","slug":"/2002-01-25-immigrants-i-see-no-immigrants/"}}},{"node":{"fields":{"title":"We Want The Airwaves For SMS","date":"24 Jan 2002","slug":"/2002-01-24-we-want-the-airwaves-for-sms/","tags":"Y2002"},"html":"<p>A couple days late again. Deutsche Telekom announced over the weekend they would be doubling (to 40 cents)  the price of sending SMS messages for new customers, then withdrew the plans Monday after a huge media and political backlash. The mean old magenta T would be taking advantage of all those teenagers. As if the cell providers don't already... one radio report mentioned offhand that SMS generates one-third of income from cellular networks.</p>"},"next":{"fields":{"title":"Schilly To Stay","date":"24 Jan 2002","slug":"/2002-01-24-schilly-to-stay/"}},"previous":{"fields":{"title":"Experimental Failure","date":"25 Jan 2002","slug":"/2002-01-25-experimental-failure/"}}},{"node":{"fields":{"title":"Schilly To Stay","date":"24 Jan 2002","slug":"/2002-01-24-schilly-to-stay/","tags":"Y2002"},"html":"<p>Schilly has admitted that his ministry made some serious mistakes in the 'NDP Informant Scandal', but has no intention of resigning. Today the press seems to have backed off, so maybe the pressure is off.</p>"},"next":{"fields":{"title":"Christopher's First Interview","date":"24 Jan 2002","slug":"/2002-01-24-christophers-first-interview/"}},"previous":{"fields":{"title":"We Want The Airwaves For SMS","date":"24 Jan 2002","slug":"/2002-01-24-we-want-the-airwaves-for-sms/"}}}],"pathPrefix":"","first":false,"last":false,"index":937,"pageCount":1101}}

